import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanActivate, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { UsuariosService } from '../services/usuarios.service';
import { Nivel } from '../enums/nivel.enum';

@Injectable({
  providedIn: 'root'
})
export class CoordenadorGuard implements CanActivate {

  constructor(private usuariosService:UsuariosService, private router:Router) { }

  async canActivate() {
    let canActivate = await this.usuariosService.checaLogin(Nivel.Coordenador);
    if (!canActivate) this.router.navigateByUrl('/inicial')
    return canActivate;
  }
  
}
