import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Nivel } from '../enums/nivel.enum';
import { HttpClient } from '@angular/common/http';
import { Paciente } from '../models/paciente';
import { firebaseURLFunctions } from 'src/config/firebase';
import { Events } from '@ionic/angular';


@Injectable({
  providedIn: 'root'
})
/**
 * Controla os dados do usuário
 */
export class UsuariosService {

  private pacientesDB:firebase.firestore.CollectionReference;
  private usuariosDB:firebase.firestore.CollectionReference;
  private usuario: firebase.User;

  constructor(private http:HttpClient, private events:Events) { 
    this.pacientesDB = firebase.firestore().collection('pacientes');
    this.usuariosDB = firebase.firestore().collection('usuarios');
    firebase.auth().onAuthStateChanged(user => {
      this.usuario = user
      if (this.usuario != null)
        console.log(this.usuario.uid);
      else
        console.log('deslogado')
    });
  }

  /**
   * Autentica o usuário
   * @param codigo 
   * @param senha 
   */
  public async logar(codigo:number, senha:string) {
    let email = codigo+'@cwg.app';
    return firebase.auth().signInWithEmailAndPassword(email, senha)
      .then(async resposta => {
        let doc = await this.usuariosDB.doc(resposta.user.uid).get();

        //Checa se o usuário foi excluido
        if (doc.data().excluido) {
          firebase.auth().signOut();
          return {sucesso:false, coordenador:0}
        }
        
        return {sucesso:true, coordenador:(doc.data().nivel == 1)}
     }).catch(erro => {
      return {sucesso:false, coordenador:false}
    })
  }

  /**
   * Checa se o usuário pode acessar os conteúdos do nivel relacionado
   * @param nivel Nivel
   */
  public async checaLogin(nivel:Nivel) {

    let user = firebase.auth().currentUser;
    if (user == null)
      return false;

    
    let usuario = await this.usuariosDB.doc(user.uid).get()
    //Checa se o usuário não foi excluido
    if (usuario.data().excluido) {
      firebase.auth().signOut();
      return false;
    }
    //Checa se o nivel está correto 
    return (usuario.data().nivel == nivel)
  }

  /**
   * Retorna o nivel do usuário
   * @return nivel Nivel
   */
  public async checaNivel(): Promise<1|2> {

    let user = firebase.auth().currentUser;
    
    //Checa se o nivel está correto
    let usuario = await this.usuariosDB.doc(user.uid).get()
    return (usuario.data().nivel)
  }

  /**
   * Cadastra um novo Usuário (Coordenador ou Paciente)
   * @param senha 
   * @param nome 
   * @param nivel 
   * @param mediaCigarros 
   * @param precoCigarro 
   */
  public async cadastrar(dados:any, nivel:Nivel = Nivel.Coordenador): Promise<any> {
    console.log(this.usuario.uid);
    if (nivel == Nivel.Paciente) {
      //dados -> nome, senha, mediaCigarros, precoCigarro, ultimoDiaRespondido
      return this.http.post(firebaseURLFunctions+'criarPaciente', {...dados}, {headers: {'user':this.usuario.uid}}).toPromise()
    }
    return null; //Não há cadastro de coordenador habilitado pelo sistema
  }

  /**
   * Atualiza o Perfil do Usuário
   */
  public async atualizar(usuarioID: string, dados:any, nivel:Nivel = Nivel.Coordenador): Promise<any> {
    
    //Checa se é o próprio usuario
    if (usuarioID == this.usuario.uid) {

      //Atualiza a senha
      firebase.auth().currentUser.updatePassword(dados.senha);
      //Atualiza o nome
      this.usuariosDB.doc(usuarioID).update({
        nome:dados.nome
      })
      if (nivel == Nivel.Paciente) {
        //Atualiza o Paciente
        return this.pacientesDB.doc(usuarioID).update({
          nome: dados.nome,
          mediaCigarros:dados.mediaCigarros,
          precoCigarro:dados.precoCigarro
        }).then(() => {return {sucess:true}})
      }
    
    } else { //Não é o próprio usuário logado
      return this.http.post(firebaseURLFunctions+'alterarPaciente', {
        pacienteID:usuarioID,
        nome:dados.nome,
        senha:dados.senha,
        mediaCigarros:dados.mediaCigarros,
        precoCigarro:dados.precoCigarro,
        ultimoDiaRespondido:dados.ultimoDiaRespondido
      }, {headers: {'user': this.usuario.uid}}).toPromise();
    }
  }

  /**
   * Atualiza a senha do usuário
   */
  public atualizarSenha(novaSenha:string):Promise<any> {
    return this.usuario.updatePassword(novaSenha);
  }
  
  /**
   * Desloga o usuário logado
   */
  public deslogar() {
    firebase.auth().signOut()
  }
}
