import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { PacienteGuard } from './guards/paciente.guard';
import { CoordenadorGuard } from './guards/coordenador.guard';

const routes: Routes = [
  { path: '', redirectTo: 'inicial', pathMatch: 'full' },
  { path: 'inicial', loadChildren: './inicial/inicial.module#InicialPageModule' },
  { path: 'login', loadChildren: './login/login.module#LoginPageModule' },
  
  { path: 'paciente', canActivate:[PacienteGuard], children: [
    { path: '', loadChildren: './paciente/paciente.module#PacientePageModule' },
    { path: 'perfil/edicao', loadChildren: './paciente/perfil/perfil-edicao/perfil-edicao.module#PerfilEdicaoPageModule' },
  ] },

  { path: 'coordenador', canActivate:[CoordenadorGuard], children: [
    { path: '',  loadChildren: './coordenador/coordenador.module#CoordenadorPageModule'},
    { path: 'sessao', loadChildren: './coordenador/sessao/sessao.module#SessaoPageModule' },
    { path: 'sessao-edicao', loadChildren: './coordenador/sessao-edicao/sessao-edicao.module#SessaoEdicaoPageModule' },
    { path: 'pacientes/novo', loadChildren: './coordenador/pacientes/paciente-edicao/paciente-edicao.module#PacienteEdicaoPageModule' },
    { path: 'pacientes/editar', loadChildren: './coordenador/pacientes/paciente-edicao/paciente-edicao.module#PacienteEdicaoPageModule' },
    { path: 'pacientes/visualizar', loadChildren: './coordenador/pacientes/visualizar-paciente/visualizar-paciente.module#VisualizarPacientePageModule' },  
  ] },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
